@import "../../variables.modules.scss";

.container {width:14vw; display: flex; flex-direction: column; align-items: center;}
.container svg {width:7vw; display: block;}

.ArrowRight svg {
  animation-name: swim;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  transform-origin: center center;
  transform-box: fill-box;
}
.ArrowLeft svg {
  animation-name: swim;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  transform-origin: center center;
  transform-box: fill-box;
}
.ArrowDown svg {
  animation-name: swim;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  transform-origin: center center;
  transform-box: fill-box;
}
.ArrowUp svg {
  animation-name: swim;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  transform-origin: center center;
  transform-box: fill-box;
}

.ArrowLeft {
  transform: skew(10deg);
  transition: transform 0.6s ease-in-out;
}

.ArrowRight {
  transform: skew(-10deg);
  transition: transform 0.6s ease-in-out;;
}

@keyframes swim {
  0%, 100% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(1.1, 0.9);
  }
  50% {
    transform: scale(1, 1);
  }
  75% {
    transform: scale(0.9, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}